:root {
  background-color: var(--main-bg);
  color: var(--main-txt-cl);
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 1.75;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  min-height: 100%;
}

h2, p {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  color: currentColor;
  cursor: pointer;
}

.btn {
  min-width: 95px;
  min-height: 44px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  padding: 10px 20px;
  font-weight: 600;
  transition: background-color .3s, border-color .3s, color .3s;
}

.primary-btn {
  border-color: #02897a;
}

.primary-btn:hover, .primary-btn:focus, .secondary-btn {
  color: #fff;
  background-color: #02897a;
}

.secondary-btn:hover, .secondary-btn:focus {
  color: currentColor;
  background-color: rgba(0, 0, 0, 0);
  border-color: #02897a;
}

.logo {
  align-items: center;
  gap: 15px;
  font-size: 30px;
  font-weight: 700;
  line-height: .93;
  display: inline-flex;
}

.header {
  box-shadow: var(--shadow);
  padding: 18px 0;
}

.header-container {
  align-items: center;
  display: flex;
}

.header-logo {
  margin-right: 105px;
}

.header-nav {
  margin-right: auto;
}

.nav {
  align-items: center;
  gap: 25px;
  display: flex;
}

.nav-link {
  padding: 8px 0;
  transition: color .3s;
  display: block;
}

.nav-link:hover, .nav-link:focus {
  color: #02897a;
}

.header-controls {
  align-items: center;
  gap: 25px;
  margin-right: 25px;
  display: flex;
}

.switcher {
  background-color: rgba(2, 137, 122, .3);
  border: 2px solid #02897a;
  border-radius: 100px;
  gap: 2px;
  padding: 3px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.switcher-status {
  content: "";
  width: 100%;
  height: 100%;
  background-image: url("status.973fb18d.svg");
  background-position: center;
  background-repeat: no-repeat;
  transition: background-position .3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.switcher-control {
  z-index: 10;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
  position: relative;
}

.switcher-control.light {
  background-image: url("light.bdcbd10c.svg");
}

.switcher-control.auto {
  background-image: url("auto.df2bd496.svg");
}

.switcher-control.dark {
  background-image: url("dark.9cd16295.svg");
}

.switcher-control.light:checked ~ .switcher-status {
  background-position: 3px 50%;
}

.switcher-control.auto:checked ~ .switcher-status {
  background-position: center;
}

.switcher-control.dark:checked ~ .switcher-status {
  background-position: right 3px center;
}

.clients {
  padding: 100px 0;
}

.clients-title {
  text-align: center;
  margin-bottom: 66px;
  font-size: 40px;
}

.clients-list {
  justify-content: center;
  align-items: center;
  gap: 65px;
  display: flex;
}

